
.participant_list_container {
    margin-top: 30px;
    color : gray;

    height: calc(100vh - 170px);
    overflow-y: auto;
    overflow-x: hidden;
    
    margin-left: -15px;

    .wrapper {
        height: 60px;
        padding-left: 15px;

        .block {
            vertical-align: center;
            padding-top: 7px;
            
            .label{
                vertical-align: top;
            }

            img{
                margin-right: 10px;
            }
        }    
    }

    :hover {
        cursor: pointer;
        background-color: #cceffc;
    }
}



